import React, { useEffect, useState } from 'react'
import {useHistory} from "react-router-dom"
const ChangePlan = ({title,type, code, address}) => {
    const history = useHistory()
        const initialState = {
        account: {
            postCode: "",
            address: ""
        },
        error: {
            postCode: "",
            address: ""
        }
    }
   
    const [data, setData] = useState(initialState)
    const handleOnChange = e => {
        const newData = {...data}
        newData.account[e.currentTarget.name] = e.currentTarget.value
        setData(newData)
    }
     useEffect(() => {
         const newData = {...data}
        if(code || address){
            newData.account.postCode = code
            newData.account.address = address
            setData(newData)
        }
        // eslint-disable-next-line
    }, [])
    const goToDashBoard = () => {
        history.push("/pages/dashboard.html")
    }
    return (
        <div className="container__edit-account">
            <h1 className="margin-bottom">{title}</h1>
            <input
            name="postCode"
            placeholder="Post Code"
            type="text"
            className="input-box margin-bottom"
            value={data.account.postCode }
            onChange={handleOnChange}
            />
            <input
            name="address"
            placeholder="Address"
            type="text"
            className="input-box margin-bottom"
            value={data.account.address}
            onChange={handleOnChange}
            />
            <div className="buttons">
            <input
            onClick={goToDashBoard}
            className="button"
            type="submit"
            value={type}
            />
            <input
            onClick={goToDashBoard}
            className="button-black
            button"
            type="submit" value="Cancel" 
            />
            </div>
      </div> 
    )
}

export default ChangePlan
