import React, { useEffect } from 'react'
import {Link} from "react-router-dom"
import {useSelector, useDispatch} from "react-redux"
import {changingActiveClass} from "../../store/global/app/header"
import {showSignUpPopup} from "../../store/global/app/popup"
import SignIn from './auth/SignIn'
import Signup from './auth/Signup'
import ForgotPassword from './auth/ForgotPassword'

const Header = () => {

  const {name: headerName} = useSelector(state => state.app.header)
  const dispatch = useDispatch()
  const handleOnClick = name => {
    dispatch(changingActiveClass({name}))
  }
  useEffect(() => {
    dispatch(changingActiveClass({name: window.location.pathname}))
    // eslint-disable-next-line
  }, [])
  const handleShow = () => {
    dispatch(showSignUpPopup({showSignUp: true}))
  }
    return (
      <>
        <div className="sticky">
        <header className="header" id="header">
          <Link to="/">
            <figcaption>
              <img onClick={() => handleOnClick("/")} className="logo" src="/img/logo.png" alt="Logo" />
            </figcaption>
          </Link>
          <nav className="navigation">
            <ul className="nav-list">
              <li className="nav-item">
                <Link
                onClick={() => handleOnClick("/")} 
                to="/" 
                className={`nav-link ${headerName === "/" ? "active" : ""}`}
                >Home</Link>
              </li>
              <li className="nav-item">
                <Link
                onClick={() => handleOnClick("/pages/faq.html")} 
                to="/pages/faq.html" className={`nav-link ${headerName === "/pages/faq.html" ? "active" : ""}`}>FAQ</Link>
              </li>
              <li className="nav-item">
                <Link
                onClick={() => handleOnClick("/pages/new-homepage.html")} 
                to="/pages/new-homepage.html" 
                className={`nav-link ${headerName === "/pages/new-homepage.html" || headerName === "/pages/static"? "active" : ""}`}
                >News</Link>
              </li>
              <li className="nav-item">
                <Link
                onClick={() => handleOnClick("/pages/pricing-plan.html")} 
                to="/pages/pricing-plan.html" 
                className={`nav-link ${headerName === "/pages/pricing-plan.html" ? "active" : ""}`}
                >Pricing</Link>
              </li>
              <li className="nav-item">
                <Link
                onClick={() => handleOnClick("/pages/contact.html")} 
                to="/pages/contact.html" 
                className={`nav-link ${headerName === "/pages/contact.html" ? "active" : ""}`}
                >Contact</Link>
              </li>
              <li className="nav-item">
                <Link
                onClick={() => handleOnClick("/pages/dashboard.html")} 
                to="/pages/dashboard.html" 
                className={`nav-link 
                ${
                  headerName === "/pages/dashboard.html"  ||
                  headerName === "/pages/create-plan.html"  ||
                  headerName === "/pages/edit-plan.html"  ||
                  headerName === "/pages/delete-plan.html"                
                ? "active" : ""}`}
                >Dashboard</Link>
              </li>
               <li className="nav-item">
                <Link
                onClick={() => handleOnClick("/pages/profile.html")} 
                to="/pages/profile.html" 
                className={`nav-link ${
                  headerName === "/pages/profile.html"  ||
                  headerName === "/pages/edit-profile.html"  ||
                  headerName === "/pages/edit-billing.html"  
                  ? "active" : ""}`}
                >Account</Link>
              </li>
                <li className="nav-item">
                <Link
                onClick={() => handleOnClick("/pages/subscription-page.html")} 
                to="/pages/subscription-page.html" 
                className={`nav-link ${
                  headerName === "/pages/subscription-page.html" 
                  ? "active" : ""}`}
                >Subscription</Link>
              </li>
              <li className="nav-item">
                <Link to="#" onClick={handleShow} className="nav-link button" style={{color: 'white'}}>Sign Up</Link>
              </li>
            </ul>
          </nav>
        </header>
      </div>
      <SignIn />
      <Signup />
      <ForgotPassword />
      </>
    )
}
export default Header
