import React from 'react'
import GuideComponent from './Sliders/GuideComponent'

const Guide = () => {
    const data = [
        {count: 1, heading: "Layout Design", para: "Use the 2D mode to create floor plans and design layouts with furniture and other home items, or switch to 3D to explore and        edit your design from any angle.", img: "/img/slider1.png"},
        {count: 2, heading: "Layout Design", para: "Use the 2D mode to create floor plans and design layouts with furniture and other home items, or switch to 3D to explore and        edit your design from any angle.", img: "/img/pngegg.png"},
        {count: 3, heading: "Layout Design", para: "Use the 2D mode to create floor plans and design layouts with furniture and other home items, or switch to 3D to explore and        edit your design from any angle.", img:"/img/asd.png"}
    ]
    return (
        <>
         <div className="full__width">
        <section className="guide__section">
          <div className="skew">
            <h1 className="guide__section-heading">
              Home design made Just 3 steps Away
            </h1>
            {data.map(field => (
                <GuideComponent
                key={field.count}
                count={field.count}
                heading={field.heading}
                para={field.para}
                img={field.img}
                />
            ))}
          </div>
        </section>
      </div>   
        </>
    )
}

export default Guide
