import React, { useEffect, useState } from 'react'
import SelectOptions from '../common/SelectOptions'

const EditProfile = () => {
  const [showPass, setShowPass] = useState(false)
      const initialState = {
        account: {
            username: "",
            email: "",
            pass: "",
        }
    }
    const [data, setData] = useState(initialState)
      const handleOnChange = (e) => {
        const newData = {...data}
        newData.account[e.currentTarget.name] = e.currentTarget.value
        setData(newData)
    }
    const countryList = ["Country", "Pakistan", "China", "USA"]
    const cityList = ["City","Lahore", "Karachi", "Faisalabad"]
    const [country, setCountry] = useState("")
    const [city, setCity] = useState("")
    useEffect(() => {
        const newData = {...data}
        newData.account.username="user1234"
        newData.account.email="user@gmail.com"
        newData.account.pass="something is here"
        setData(newData)
        // eslint-disable-next-line
    }, [])
    return (
        <div className="user-info">
        <div className="user-photo">
          <svg className="icon">
            <use xlinkHref="../img/sprite.svg#profile-user" />
          </svg>
          <button className="button">Edit Picture</button>
        </div>
        <form className="form">
          <input
          type="text"
          className="input"
          name="username"
          value={data.account.username}
          onChange={handleOnChange}
          />
          <input
          type="email"
          className="input"
          name="email"
          value={data.account.email}
          onChange={handleOnChange}
          />
          <div className="toggle-password">
            <input
            type={showPass ? "text": "password"}
            name="pass"
            className="input"
            value={data.account.pass }
            onChange={handleOnChange}
            id="password"
            />
            <i className="far fa-eye" id="togglePassword" onClick={() => setShowPass(!showPass)}/>
          </div>
          <div className="selectdiv">
            <SelectOptions
            value={country}
            onChange={e => setCountry(e.target.value)}
            list={countryList} 
            />
          </div>
          <div className="selectdiv">
             <SelectOptions
            value={city}
            onChange={e => setCity(e.target.value)}
            list={cityList} 
            />
          </div>
        </form>
      </div>
    )
}

export default EditProfile
