import React, { useState } from 'react'
import HomeSlider from './Sliders/HomeSlider';

const Slider1 = () => {
const [sliderNumber, setSliderNumber] = useState(1)
const data = [
    {id: 1,heading: "Create Your Dream Home", para:"An advanced and easy-to-use 2D/3D home design tool. Join a community of 64&nbsp;668&nbsp;478 amateur designers.", start:"Start Now", img:"/img/slider1.png", active: sliderNumber === 1 && "active"},
    {id: 2,heading: "Slide 2", para:"An advanced and easy-to-use 2D/3D home design tool. Join a    community of 64&nbsp;668&nbsp;478 amateur designers.", start:"Start Now", img:"/img/slider1.png",
    active: sliderNumber === 2 && "active"
    },
    {id: 3,heading:"Slide 3", para:"An advanced and easy-to-use 2D/3D home design tool. Join a    community of 64&nbsp;668&nbsp;478 amateur designers.", start:"Start Now", img:"/img/slider1.png",active: sliderNumber === 3 && "active"}
]
    return (
    <div className="container">
       <section className="home__slider">
          {data.map(field => (
              <HomeSlider 
              key={field.id}
              heading={field.heading}
              para={field.para}
              start={field.start}
              img={field.img}
              active={field.active}
              />
          ))}
           
        <div className="home__slider-dots">
          <div className={`dot ${sliderNumber === 1 && "active"}`} onClick={() => setSliderNumber(1)}/>
          <div className={`dot ${sliderNumber === 2 && "active"}`} onClick={() => setSliderNumber(2)}/>
          <div className={`dot ${sliderNumber === 3 && "active"}`} onClick={() => setSliderNumber(3)}/>
        </div>
      </section>
    </div>
    )
}

export default Slider1
