import React from 'react'

const SelectOptions = ({list, onChange, value}) => {
    return (
        <select 
        value={value}
        onChange={onChange}
        className="select input">
            {list.map(item => (
                <option 
                disabled={item === "City" || item === "Country" ? true : false} 
                defaultValue={item === "City" || item === "Country" ? true : false} 
                hidden={item === "City" || item === "Country" ? true : false}
                key={item} 
                value={item === "City" || item === "Country" ? "" : item}
                >{item}</option>
            ))}
        </select>
    )
}

export default SelectOptions
