import React, { useEffect } from 'react'
import Plan from './Plan'

const Plans = ({handlePlansCount}) => {
    const data = [
        {number: 1, name: "Plan Name", address: "Plan Address", code: 50280},
        {number: 2, name: "Plan Name", address: "Plan Address", code: 50280},
        {number: 3, name: "Plan Name", address: "Plan Address", code: 50280},
        {number: 4, name: "Plan Name", address: "Plan Address", code: 50280},
        {number: 5, name: "Plan Name", address: "Plan Address", code: 50280}
    ]
    useEffect(() => {
        handlePlansCount(data.length)
        //eslint-disable-next-line
    }, [])
    return (
        <>
         {data.map(field => (
             <Plan 
             key={field.number}
             number={field.number}
             name={field.name}
             address={field.address}
             code= {field.code}
             />
         ))}   
        </>
    )
}

export default Plans
