import React from 'react'

const UserProfile = () => {
    return (
        <>
         <div className="user-info">
          <div className="user-photo">
            <svg className="icon">
              <use xlinkHref="../img/sprite.svg#profile-user" />
            </svg>
            <p className="username">user1234</p>
            <p className="useremail">user@gmail.com</p>
          </div>
          <form className="form">
            <input type="text" className="input" value="user1234" disabled="disabled" />
            <input type="email" className="input" value="user@gmail.com" disabled="disabled" />
            <div className="toggle-password">
              <input type="password" className="input" value="meri marzi" id="password" disabled="disabled" />
              <i className="far fa-eye" id="togglePassword" />
            </div>
            <input type="text" className="input" value="Pakistan" disabled="disabled" />
            <input type="text" className="input" value="Lahore" disabled="disabled" />
          </form>
        </div>   
        </>
    )
}

export default UserProfile
