import React from 'react'

const GuideComponent = ({count, heading, para, img}) => {
    return (
        <>
         <div className="guide__section-steps guide__section-step1">
              <span className="serial">{count}</span>
              <div className="detail">
                <h2>{heading}</h2>
                <p>{para}
                </p>
              </div>
              <div className="images">
                <div className="img img-1"><img src={img} alt="" /></div>
                <div className="img img-2"><img src={img} alt="" /></div>
              </div>
            </div>   
        </>
    )
}

export default GuideComponent
