import React from 'react'
import { Link } from 'react-router-dom'

const Terms = () => {
    return (
        <>
            <div className="signup-disclaimer">
            <p>
              By Signing Up you agree to our <Link to="#" >Terms of Services</Link>,
              including <Link to="#" >privacy Policy</Link>
            </p>
            <p>
              and <Link to="#" >Cookies Policy</Link> Cookies Policy. Learn how we
              collect, use and share your
            </p>
            <p>
              data in our <Link to="#" >Privacy Policy</Link> and how we use cookies and
              similar technology in our <Link to="#" >Cookies Policy</Link> .
            </p>
          </div>   
        </>
    )
}

export default Terms
