import React from 'react'

const SocialLinks = () => {
    return (
        <>
           <div className="social-links">
            <div className="likes">
              <span className="icon"><ion-icon size="large" name="heart-outline" /></span>
              <span>127 Likes</span>
            </div>
            <div className="social">
              <a href="https://facebook.com"><ion-icon size="large" name="logo-facebook" /></a>
              <a href="https://twitter.com"><ion-icon size="large" name="logo-twitter" /></a>
              <a href="https://linkedin.com"><ion-icon size="large" name="logo-linkedin" /></a>
            </div>
          </div>   
        </>
    )
}

export default SocialLinks
