import React from 'react'
import { Link } from 'react-router-dom'

const News = ({link, img, title, detail}) => {
    return (
        <Link to={`${link}`}>
        <div className="post__feed-content">
          <img src={`${img}`} alt="post-pic" />
          <div className="post-info">
            <span>Category Name</span>
            <span>|</span>
            <span>Date</span>
          </div>
          <h3 className="post-title">{title}</h3>
          <p className="post-detail">{detail}</p>
        </div>
      </Link>
    )
}

export default News
