import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import Billing from './billing/Billing'
import EditProfile from './editProfile/EditProfile'
import UserProfile from './profile/UserProfile'

const AccountWrapper = () => {
    const [step, setStep] = useState("/pages/profile.html")
    useEffect(() => {
        setStep(window.location.pathname)
    }, [])
    // console.log()
    return (
          <>
         <div className="container__edit-account">
            <h1 className="u-margin-bottom-small">My account</h1>
            <ul className="nav-list u-margin-bottom-medium">
            <li className="nav-item">
                <Link 
                to="/pages/profile.html" 
                className={`nav-link ${step === "/pages/profile.html" ? "active": ""}`}
                onClick={() => setStep("/pages/profile.html")}
                >Profile</Link>
            </li>
            <li className="nav-item">
                <Link 
                to="/pages/edit-profile.html" 
                className={`nav-link ${step === "/pages/edit-profile.html" ? "active": ""}`}
                onClick={() => setStep("/pages/edit-profile.html")}
                >Edit Profile</Link>
            </li>
            <li className="nav-item">
                <Link 
                to="/pages/edit-billing.html" 
                className={`nav-link ${step === "/pages/edit-billing.html" ? "active": ""}`}
                onClick={() => setStep("/pages/edit-billing.html")}
                >Billing</Link>
            </li>
            </ul>
            {step === "/pages/profile.html" ? <UserProfile />: null}
            {step === "/pages/edit-profile.html" ? <EditProfile />: null}
            {step === "/pages/edit-billing.html" ? <Billing />: null}
        
      </div>   
        </>
    )
}

export default AccountWrapper
