import React from 'react'
import { Link } from 'react-router-dom'
import { Modal } from 'react-bootstrap';
import {useSelector, useDispatch} from "react-redux"
import { showSignInPopup, hideSignUpPopup, hideSignInPopup } from '../../../store/global/app/popup';
import Terms from './Terms';
import Inputs from './Inputs';


const Signup = () => {
    const dispatch = useDispatch()
    const {signup} = useSelector(state => state.app.popup)


    const handleShowSignIn = () => {
    dispatch(hideSignUpPopup({showSignUp: false}))
    dispatch(showSignInPopup({showSignIn: true}))
  }
  const handleClose= () => {
      dispatch(hideSignUpPopup({showSignUp: false}))
      dispatch(hideSignInPopup({showSignIn: false}))
  }
    return (
        <>
            <Modal
        show={signup}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
        style={{marginTop: "25vh"}}
      >
        <div className="popup__signup" id="signup">
        <div className="popup__signup-content">
          <div className="head">
            <h3>Sign Up</h3>
            <span >
              <i 
              onClick={handleClose} 
              className="far fa-times-circle fa-2x" 
              name="close-circle-outline"  
              style={{color: "#ed0303"}}
              ></i>
            </span>
          </div>
         <Inputs />
          <div className="separator">
            <span />
            <p>OR</p>
            <span />
          </div>
       <Terms />
          <div className="have-account">
            Already have an account? <Link to="#" onClick={handleShowSignIn}>Sign in</Link>
          </div>
        </div>
      </div>
      </Modal>
        </>
    )
}

export default Signup
