import React from 'react'
import BillingFields from '../common/BillingFields'

const Billing = () => {

    return (
        <div className="main-form">
          <h2 className="u-margin-bottom-small">Billing Information</h2>
        <form className="form">
          <BillingFields account/>
          <div className="form-group-buttons form-group">
            <input className="button" type="submit" value="update" />
            <input className="button-black button" type="submit" value="cancel" />
          </div>
        </form>
      </div>
    )
}

export default Billing
