import React, { useState } from 'react'
import SecondSlider from './Sliders/SecondSlider'

const Slider2 = () => {
    const [sliderNumber, setSliderNumber] = useState(2)
    const data = [
        {id: 1, img: "/img/NoPath - Copy (18).png", active: sliderNumber === 1 && "active"},
        {id: 2, img: "/img/NoPath - Copy (18).png", active: sliderNumber === 2 && "active"},
        {id: 3, img: "/img/NoPath - Copy (18).png", active: sliderNumber === 3 && "active"},
        {id: 4, img: "/img/NoPath - Copy (18).png", active: sliderNumber === 4 && "active"},
        {id: 5, img: "/img/NoPath - Copy (18).png", active: sliderNumber === 5 && "active"}
    ]
    
    return (
        <>
         <div className="container">
			<section className="home__slider2">
				<h1 className="home__slider2-heading">CREATE ANY WHERE ANY TIME</h1>
				<div className="home__slider2-content">
					<div className="icons">
						<i onClick={() => setSliderNumber(1)} className={`icon flaticon-laptop ${sliderNumber === 1 && "active"}`}></i>

						<i onClick={() => setSliderNumber(2)} className={`icon flaticon-apple ${sliderNumber === 2 && "active"}`}></i>

						<i onClick={() => setSliderNumber(3)} className={`icon flaticon-android-logo ${sliderNumber === 3 && "active"}`}></i>

						<i onClick={() => setSliderNumber(4)} className={`icon flaticon-windows ${sliderNumber === 4 && "active"}`}></i>
                        
						<i onClick={() => setSliderNumber(5)} className={`icon flaticon-ubuntu-logo ${sliderNumber === 5 && "active"}`}></i>
					</div>

					<div className="slide-container">
					{data.map(field => (
                        <SecondSlider 
                        key ={field.id}
                        img={field.img}
                        active={field.active}
                        />
                    ))}
					</div>
				</div>
			</section>
		</div>   
        </>
    )
}

export default Slider2
