import React from 'react'

const Comment = () => {
    return (
        <>
              <div className="comment__section">
            <h1>16 comments</h1>
            <form className="form" onSubmit={e => e.preventDefault()}>
              <textarea className="input" placeholder="Write Your Response..." value={""} />
              <input type="submit" value="Submit" className="button" />
            </form>
          </div>
          <div className="comments">
            <div className="comments__head">
              <span className="user-photo">
                <ion-icon size="large" name="person-outline" />
              </span>
              
              <div className="user-detail">
                <span className="name">Name</span>
                <span className="time">5 hours ago</span>
              </div>
            </div>
            <div className="comments__body">
              Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam
              nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam
              erat, sed diam voluptua. At vero
            </div>
            <div className="comments__footer">
              <div className="box comment-likes">
                <span><ion-icon name="heart-outline" /> </span>
                <span>16</span>
              </div>
              <div className="box comment-reply">
                <span><ion-icon name="chatbox-outline" /> </span>
                <span>12</span>
              </div>
            </div>
          </div>   
        </>
    )
}

export default Comment
