import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import Input from "../common/Input"
import SelectOptions from "../common/SelectOptions"

const BillingFields = ({account, subscription}) => {
    const initialState = {
        account: {
            username: "",
            email: "",
            pass: "",
            cPass: "",
            fName: "",
            lName: "",
            cardNum: "",
        }
    }
    const countryList = ["Country","Pakistan", "China", "USA"]
    const [country, setCountry] = useState("")
    const [data, setData] = useState(initialState)
    const [terms, setTerms] = useState(false)
    const [getUpdates, setGetUpdates] = useState(false)

    const handleOnChange = (e) => {
        const newData = {...data}
        newData.account[e.currentTarget.name] = e.currentTarget.value
        setData(newData)
    }
    const handleOptions = (e) => {
      setCountry(e.target.value)
    }
    return (
        <>
            <div className="form-group">
            <Input
            placeholder="Username"
            type="text"
            name="username"
            value={data.account.username}
            onChange={handleOnChange}
            />
            <Input
            placeholder="Email"
            type="email"
            name="email"
            value={data.account.email}
            onChange={handleOnChange}
            />
          </div>
          <div className="form-group">
            <Input
            placeholder="Password"
            type="password"
            name="pass"
            value={data.account.pass}
            onChange={handleOnChange}
            />
            <Input
            placeholder="Confirm Password"
            type="password"
            name="cPass"
            value={data.account.cPass}
            onChange={handleOnChange}
            />
          </div>
          <div className="form-group">
            <Input
            placeholder="First Name"
            type="text"
            name="fName"
            value={data.account.fName}
            onChange={handleOnChange}
            />
            <Input
            placeholder="Last Name"
            type="text"
            name="lName"
            value={data.account.lName}
            onChange={handleOnChange}
            />
          </div>
          <div className="form-group">
           {subscription ?  
              <SelectOptions 
              value={country}
              onChange= {handleOptions}
              list={countryList}
              />: null}
              {account ?  <div className="selectdiv">
              <SelectOptions 
              value={country}
              onChange= {handleOptions}
              list={countryList}
              />
            </div>: null}
          </div>
          <div className="form-group">
            <span className="fast__payment-text">Pay With Credit/Debit Card</span>
            <Link to="/pages/edit-billing.html" className="visa-master">
              <svg>
                <use xlinkHref="../img/sprite.svg#symbols" />
              </svg>
              <svg>
                <use xlinkHref="../img/sprite.svg#mastercard" />
              </svg>
            </Link>
            <Link to="/pages/edit-billing.html" className="paypal">
              <svg>
                <use xlinkHref="../img/sprite.svg#paypal" />
              </svg>
            </Link>
          </div>
          <div className="form-group">
            <div className="credit-card">
              <svg>
                <use xlinkHref="../img/sprite.svg#credit-card" />
              </svg>
              <Input 
              creditStyle="credit-input"
              placeholder="Card Number MM/YY CVC"
              type="text"
              name="cardNum"
              value={data.account.cardNum}
              onChange={handleOnChange}
              />
            </div>
          </div>
          <div className="checkbox">
            <div className="terms">
              <input type="checkbox" onChange={() => setTerms(!terms)} checked={terms} />
              <span>You Agree To Our <Link to="/pages/edit-billing.html">Terms Of Service</Link> </span>
            </div>
            <div className="updates">
              <input type="checkbox" onChange={() => setGetUpdates(!getUpdates)} checked={getUpdates}/>
              <span>Get Updates Via Email.</span>
            </div>
          </div>
        </>
    )
}

export default BillingFields
