import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { Modal } from 'react-bootstrap';
import {useSelector, useDispatch} from "react-redux"
import { showSignInPopup, hideSignUpPopup, hideSignInPopup, hideForgotPasswordPopup } from '../../../store/global/app/popup';
import Terms from './Terms';

const ForgotPassword = () => {
    const [email, setEmail] = useState("")
    const dispatch = useDispatch()
    const {forgotPassword} = useSelector(state => state.app.popup)

   const handleClose= () => {
      dispatch(hideSignUpPopup({showSignUp: false}))
      dispatch(hideSignInPopup({showSignIn: false}))
      dispatch(hideForgotPasswordPopup({showForgotPassword: false}))
  }
  const handleShowSignIn = () => {
    dispatch(hideForgotPasswordPopup({showForgotPassword: false}))
    dispatch(showSignInPopup({showSignIn: true}))
  }
    return (
        <>
        <Modal
        show={forgotPassword}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
        style={{marginTop: "25vh"}}
      >
         <div className="popup__forgot" id="forgot">
        <div className="popup__forgot-content">
          <div className="head">
            <h3>Forgot Password</h3>
            <span >
              <i 
              onClick={handleClose} 
              className="far fa-times-circle fa-2x" 
              name="close-circle-outline"  
              style={{color: "#ed0303"}}
              ></i>
            </span>
          </div>
          <form className="form">
            <input
            value={email}
            onChange={e => setEmail(e.target.value)}
            className="input"
            type="email"
            placeholder="Email"
             />
            <input
            className="button"
            type="submit"
            defaultValue="SUBMIT"
            />
            <input
            className="button button-black"
            type="submit"
            defaultValue="CANCEL"
            />
          </form>
          <Terms />
          <div className="have-account">
            Already have an account? <Link onClick={handleShowSignIn} to="#">Sign in</Link>
          </div>
        </div>
      </div>   
      </Modal>
        </>
    )
}

export default ForgotPassword
