import {createSlice} from "@reduxjs/toolkit"

//////////////////////////////////////////////////////////////////////////////
//              Reducers
//////////////////////////////////////////////////////////////////////////////

const slice = createSlice({
    name:"popup",
    initialState: {
        signup: false,
        signin: false,
        forgotPassword: false
    },
    reducers: {
        showSignUpPopup: (popup,action) => {
            popup.signup = action.payload.showSignUp
        },
        hideSignUpPopup: (popup, action) => {
            popup.signup = action.payload.showSignUp
        },
        showSignInPopup: (popup,action) => {
            popup.signin = action.payload.showSignIn
        },
        hideSignInPopup: (popup, action) => {
            popup.signin = action.payload.showSignIn
        },
        showForgotPasswordPopup: (popup,action) => {
            popup.forgotPassword = action.payload.showForgotPassword
        },
        hideForgotPasswordPopup: (popup, action) => {
            popup.forgotPassword = action.payload.showForgotPassword
        }
    }
})

export default slice.reducer
//////////////////////////////////////////////////////////////////////////
//                          Actions
//////////////////////////////////////////////////////////////////////////

export const {
    showSignUpPopup,
    hideSignUpPopup,
    showSignInPopup,
    hideSignInPopup,
    showForgotPasswordPopup,
    hideForgotPasswordPopup
} = slice.actions