import {createSlice} from "@reduxjs/toolkit"


///////////////////////////////////////////////////////////////////////////////////
//                      Reducers
//////////////////////////////////////////////////////////////////////////////////
const slice = createSlice({
    name: "header",
    initialState: {
        name: "/"
    },
    reducers: {
        changeActiveClass: (header, action) => {
            header.name = action.payload.name
        }
    }
})

export default slice.reducer


///////////////////////////////////////////////////////////////////////////////////
//                      Actions
//////////////////////////////////////////////////////////////////////////////////

const {changeActiveClass} = slice.actions

export const changingActiveClass = name => changeActiveClass(name)