import React, { useState } from 'react'
import {Link} from "react-router-dom"
import Plans from './plans/Plans'
import PlansNavbar from './plans/PlansNavbar'
const DashBoard = () => {
  const [count, setCount] = useState(0)
  const handlePlansCount = num => {
    setCount(num)
  }
    return (
        <>
        <div className="dashboard__container">
        <div className="plans__head">
          <div className="plans__head-heading">
            <h1>my plans</h1>
            <Link to="/pages/create-plan.html" className="button">create plan</Link>
          </div>
          <div className="plans__head-search">
            <input className="input-search" type="text" placeholder="Search Plans" />
            <svg className="icon">
              <use xlinkHref="/img/sprite.svg#filter-1" />
            </svg>
          </div>
        </div>
        <div className="plans__body">
          <div className="user-nav">
            <Link to="#" className="nav-link active">All Plans</Link>
            <Link to="#" className="nav-link">Recent Plans</Link>
          </div>
          <div className="user-plans">
            <PlansNavbar />
            <div className="plans-list">
              <ul>
                <Plans
                handlePlansCount = {handlePlansCount}
                />
              </ul>
            </div>
          </div>
        </div>
        <div className="plans__footer">
          <span>{count} Plans found</span>
        </div>
      </div>   
        </>
    )
}

export default DashBoard
