import React from 'react'
import { Link } from 'react-router-dom'
import Feature from './Sliders/Feature'

const Features = () => {
    const data = [
        {id: 1, heading: "HD Vizualizations", style:"box1", img:"img/Icon feather-image.png", para: "Use the Snapshots feature to capture your design as a realistic image."},
        {id: 2, heading: "2D/3D Modes", style:"box2", img:"img/Icon ionic-md-cube.png", para: "Experiment with both 2D and 3D views as you design from various angles."},

        {id: 3, heading: "5000+ items catalog", style:"box3", img:"img/Icon map-convenience-store.png", para: "Explore our project gallery and browse our content. We have something great."},

        {id: 4, heading: "Easy User Interface", style:"box4", img:"img/Icon feather-layout.png" , para: "The intuitive and user-focused interface provides an easy design process."},

        {id: 5, heading: "Item Editing", style:"box5", img:"img/Icon awesome-brush.png", para: "Apply custom colors, patterns and materials to furniture, walls and floors."},
        {id: 6, heading: "Best Community", style:" box6", img:"img/Icon awesome-users.png", para: " Be a part of a growing community. Upload and customize projects. Get inspired."},
    ]
    return (
        <div>
         <div className="full__width">
        <section className="feature-ellipse">
          <div className="feature-container">
            <h1 className="feature-heading">Created for Amateurs</h1>
            <div className="feature-content">
            {data.map(field => (
                <Feature 
                key={field.id}
                img={field.img}
                style={field.style}
                heading={field.heading}
                para={field.para}
                />
            ))}
            </div>
          </div>
        </section>
      </div>   
      <div className="create__button u-margin-bottom-medium">
				<Link to="/" className="button">create now</Link>
			</div>
        </div>
    )
}

export default Features
