import React from 'react'

const PlansNavbar = () => {
    return (
        <>
         <div className="plans-head">
              <div className="checkbox">
                <input className="input-checkbox" type="checkbox" />
                <svg className="icon">
                  <use xlinkHref="/img/sprite.svg#arrow-down-sign-to-navigate" />
                </svg>
              </div>
              <div className="plans-tag">
                <p>Plans</p>
                <svg className="icon">
                  <use xlinkHref="/img/sprite.svg#arrow-down-sign-to-navigate" />
                </svg>
              </div>
              <span className="plans-address">Address</span>
              <span className="plans-code">Postal Code</span>
              <span>Actions</span>
            </div>   
        </>
    )
}

export default PlansNavbar
