import React, { useState } from 'react'

const FaqItem = ({question, answer}) => {
    const [show, setShow] = useState(false)
    return (
        <div className="faq-item">
              <div className="faq-link">
                {question}
                {!show ? <ion-icon
                onClick={() => setShow(!show)} className="add" name="add-outline" /> : 
                <ion-icon 
                onClick={() => setShow(!show)}
                className="remove" name="remove-outline" />}
              </div>
              <div className="answer" style={{maxHeight: `${show? "95": "0"}px`}}>
                <p>
                  {answer}
                </p>
              </div>
        </div>
    )
}

export default FaqItem
