import React from 'react'
import { Link } from 'react-router-dom'

const HomeSlider = ({heading, para, start, img, active}) => {
    return (
        <>
         <div className={`home__slider-slide ${active}`}>
          <div className="home__slider-text">
            <h1 className="heading">{heading}</h1>
            <p>
              {para}
            </p>
            <Link to="/"  className="button">{start}</Link>
          </div>
          <div className="home__slider-content">
            <figcaption>
              <img src={img} alt="" />
            </figcaption>
          </div>
        </div>   
        </>
    )
}

export default HomeSlider