import React from 'react'
import { Link } from 'react-router-dom'
import { Modal } from 'react-bootstrap';
import {useSelector, useDispatch} from "react-redux"
import { showSignUpPopup, hideSignUpPopup, hideSignInPopup, showForgotPasswordPopup, hideForgotPasswordPopup } from '../../../store/global/app/popup';
import Terms from './Terms';
import Inputs from './Inputs';

const SignIn = () => {
        const dispatch = useDispatch()
    const {signin} = useSelector(state => state.app.popup)

   const handleClose= () => {
      dispatch(hideSignUpPopup({showSignUp: false}))
      dispatch(hideSignInPopup({showSignIn: false}))
      dispatch(hideForgotPasswordPopup({showForgotPassword: false}))
  }
  const handleShowSignup = () => {
    dispatch(hideSignInPopup({showSignIn: false}))
    dispatch(showSignUpPopup({showSignUp: true}))
  }
  const handleShowForgotPassword = () => {
    dispatch(hideSignInPopup({showSignIn: false}))
    dispatch(showForgotPasswordPopup({showForgotPassword : true}))
  }
    return (
        <>
        <Modal
        show={signin}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
        style={{marginTop: "25vh"}}
      >
        <div className="popup__signin" id="signin">
        <div className="popup__signin-content">
          <div className="head">
            <h3>Sign in</h3>
            <span >
              <i 
              onClick={handleClose} 
              className="far fa-times-circle fa-2x" 
              name="close-circle-outline"  
              style={{color: "#ed0303"}}
              ></i>
            </span>
          </div>
         <Inputs />
          <div className="forgot-password">
            <Link to="#" onClick={handleShowForgotPassword}>Forgot Password?</Link>
          </div>
          <Terms />
          <div className="have-account">
            Do not have an account? <Link to="#" onClick={handleShowSignup}>Sign up</Link>
          </div>
        </div>
      </div>
      </Modal>
        </>
    )
}

export default SignIn
