import React from 'react'
const Slider = ({heading, para, img, active}) => {
    return (
        <>
         <div className={`home__slider-slide ${active}`}>
          <div className="home__slider-text">
            <h1 className="heading">{heading}</h1>
            <p>
              {para}
            </p>
          </div>
          <div className="home__slider-content">
            <figcaption>
              <img src={img} alt="" />
            </figcaption>
          </div>
        </div>   
        </>
    )
}

export default Slider