import React from 'react'
import {Link} from "react-router-dom"
const PricePlan = ({style_css,title, subTitle, price, duration, para,link, detail, offers, start}) => {

    return (
        <div className={`plan ${style_css}`}>
		<h1>{title}</h1>
        <p>
          {subTitle}
        </p>
        {price && !duration ? <h1>{price}</h1>: null }
        {price && duration ? <h1>{price}<span>{duration}</span></h1>: null }
        <p>{para}</p>
        {start ? <Link to={`${link}`} className="button">Get started</Link>: null}
        <p>
            {detail}
        </p>
        <ul className="list">
            {offers.map(field => (
                <li key={field.id} className="list-item">
                 <svg className="ckeck-icon">
                   <use xlinkHref="/img/sprite.svg#check" />
                 </svg>
                 <span>{field.name}</span>
               </li>
            ))}
        </ul>
		</div>   
    )
}

export default PricePlan
