import React from 'react'
import { Link } from 'react-router-dom'

const Plan = ({number, name, address, code}) => {
    return (
        <>
            <li className="list-item">
                  <span>{number}</span>
                  <span className="plans-tag">{name}</span>
                  <span className="plans-address">{address}</span>
                  <span className="plans-code">{code}</span>
                  <span className="plan-acitons">
                    <Link to="#">
                      <svg className="icon view">
                        <use xlinkHref="/img/sprite.svg#eye" />
                      </svg>
                    </Link>
                    <Link to="/pages/edit-plan.html">
                      <i className="fas fa-edit" />
                    </Link>
                    <Link to="/pages/delete-plan.html">
                      <svg className="icon delete">
                        <use xlinkHref="/img/sprite.svg#delete" />
                      </svg>
                    </Link>
                  </span>
                </li>   
        </>
    )
}

export default Plan
