import Header from "./components/layout/Header";
import {BrowserRouter as Router, Route, Switch} from "react-router-dom"
import Home from "./components/layout/home/Home";
import Footer from "./components/layout/Footer"
import FaqIntro from "./components/layout/faq/FaqIntro";
import NewsSlider from "./components/layout/news/NewsSlider";
import NewsDetail from "./components/layout/news/NewsDetail";
import Price from "./components/layout/pricing/Price";
import ContactInfo from "./components/layout/contact/ContactInfo";
import DashBoard from "./components/user/DashBoard";
import CreatePlan from "./components/user/plans/CreatePlan";
import EditPlan from "./components/user/plans/EditPlan";
import DelatePlan from "./components/user/plans/DelatePlan";
import AccountWrapper from "./components/user/account/AccountWrapper";
import Subscription from "./components/user/account/subscription/Subscription";


function App() {
  return (
    <>
 <Router>
    <Header />
      <Switch>  <Route exact path="/" component={Home} />
      <Route exact path="/pages/faq.html" component={FaqIntro} />
      <Route exact path="/pages/new-homepage.html" component={NewsSlider} />
      <Route exact path="/pages/static" component={NewsDetail} />
      <Route exact path="/pages/pricing-plan.html" component={Price} />
      <Route exact path="/pages/contact.html" component={ContactInfo} />
      <Route exact path="/pages/dashboard.html" component={DashBoard} />
      <Route exact path="/pages/create-plan.html" component={CreatePlan} />
      <Route exact path="/pages/edit-plan.html" component={EditPlan} />
      <Route exact path="/pages/delete-plan.html" component={DelatePlan} />
      <Route 
            exact 
            path={["/pages/profile.html", "/pages/edit-profile.html", "/pages/edit-billing.html"]} component={AccountWrapper} />
      <Route exact path ="/pages/subscription-page.html" component={Subscription} /> 
      </Switch>
      <Footer />

    </Router>  
    </>
  );
}

export default App;
